
const api = {

    async getChannel() {
        let response = await fetch("http://app.endycahyono.com/firstmedia/v4/channel", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({})
        });
        let obj = await response.json();
        return obj;
    },

    async getSchedule(channelNumbers, dateStart, dateEnd) {
        let response = await fetch("http://app.endycahyono.com/firstmedia/v4/schedule", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                ChannelNumbers: channelNumbers,
                DateStart: dateStart,
                DateEnd: dateEnd
            })
        });
        let obj = await response.json();
        return obj;
    }

}

export default api;
