<template>
  <div id="app">
    <MainNavBar></MainNavBar>
    <router-view />
  </div>
</template>

<style>
</style>

<script>
import MainNavBar from "./components/MainNavBar.vue";

export default {
  components: {
    MainNavBar
  },
  created() {
    this.$store.dispatch("settings/initialize");
    window.addEventListener("storage", this.onSettingsUpdated);
  },
  beforeDestroy() {
    window.removeEventListener("storage", this.onSettingsUpdated);
  },
  errorCaptured(err, vm, info) {
    this.$store.dispatch("notification/logerror", {
      err: err,
      vm: vm,
      info: info
    });
  },
  methods: {
    onSettingsUpdated(event) {
      this.$store.dispatch("settings/parseStorageEvent", event);
    }
  }
};
</script>